import {Canvas} from "utils/canvas/Canvas";
import {V} from "Vector";

declare const Module: any;

function heatMapColorforValue(T: number) {
    const h = (1.0 - T) * 240;
    const s = 100;
    const l = 50;
    return `hsl(${h}, ${s}%, ${l}%)`;
}

const heatMap = false;

Module.onRuntimeInitialized = function() {
    const canvas = new Canvas("canvas", () => {});
    const N = Module._get_N();
    const s = 3;
    const dt = 0.25/N/N/2;
    const a = 0, b = 0, v0 = 100;
    const max = Math.max(a, b, v0);

    Module._init(a, b, v0);

    let t = 0;
    function step() {
        for (let j = 0; j < 40; j++)
            Module._step(dt);

        canvas.draw((p) => {
            for (let i = 0; i < N; i++) {
                p.setFillColor(heatMapColorforValue(Module._val(i)/max));
                if (heatMap) {
                    p.fillRect(V(i*s - N*s/2, t/20 - canvas.height/2 + 5), V(s, s));
                } else {
                    p.fillRect(V(i*s - N*s/2, 0), V(s, 50));
                }
            }
        }, !heatMap);
        t++;

        requestAnimationFrame(step);
    }
    requestAnimationFrame(step);
}